import React from 'react'
import { CardElement, injectStripe } from 'react-stripe-elements-universal'
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import styled from "styled-components"

import Box from "../components/UI/Box/Box";
import Button from "../components/UI/Button/Button";
import TextField from "../components/UI/TextField/TextField";
import su2cTheme from '../components/UI/themes/su2c';
import Label from "../components/UI/Label"

const SignupSchema = Yup.object().shape({
  firstName: Yup.string()
    .min(2, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  lastName: Yup.string()
    .min(2, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  email: Yup.string()
    .email("Invalid email")
    .required("Required")
});

const StyledCardElement = styled(CardElement)`
  background-color: #fff;
  background-image: none;
  border-radius: 3px;
  border: solid 2px #c8c9c7;
  color: #333;
  display: block;
  font-size: 1rem;
  padding: 6px 8px;
  width: 100%;
`;

class CustomCheckoutForm extends React.Component {
	constructor(props) {
		super(props)
	}
	componentDidMount() {
		if((typeof window !== `undefined`) && (typeof window.Stripe !== `undefined`)) {
			this.stripe = window.Stripe(`my_key`, {
				betas: ['payment_intent_beta_3']
			})
		}
	}
	async handleSubmit(event) {
		event.preventDefault()
		const { paymentIntent, error } = await this.stripe.handleCardPayment(
	 		`a client_secret`,
			this.StripeCardElement
    )
    // .then(...and so on)
	}
	render() {
		return (
      <Formik
        initialValues={{
          donationAmount: "",
          firstName: "",
          lastName: "",
          email: ""
        }}
        validationSchema={SignupSchema}
        onSubmit={values => {
          // same shape as initial values
          console.log(values);
        }}
      >
        {({ errors, touched, values, handleChange, handleSubmit, setFieldValue }) => (
          <Form>
            <div>
              <Button
                type="button"
                appearance={values.donationAmount === 20 ? "primary" : "defailt"}
                onClick={() => setFieldValue("donationAmount", 20)}
              >
                £20
              </Button>
              <Button
                type="button"
                appearance={values.donationAmount === 50 ? "primary" : "defailt"}
                onClick={() => setFieldValue("donationAmount", 50)}
              >
                £50
              </Button>
              <Button
                type="button"
                appearance={values.donationAmount === 100 ? "primary" : "defailt"}
                onClick={() => setFieldValue("donationAmount", 100)}
              >
                £100
              </Button>
            </div>
            <TextField
              label="Donation amount"
              extraLeft="£"
              name="donationAmount"
              onChange={handleChange}
              value={values.donationAmount}
              error={errors.donationAmount}
              type="number"
            />
            <TextField
              label="First name"
              name="firstName"
              onChange={handleChange}
              value={values.firstName}
              error={errors.firstName}
            />
            <TextField
              label="Last name"
              name="lastName"
              onChange={handleChange}
              value={values.lastName}
              error={errors.lastName}
            />
            <Label>Card details
              <StyledCardElement ref={(thisCardElement) => {this.StripeCardElement = thisCardElement}} />
            </Label>
            <Button type="submit" appearance="primary" onClick={handleSubmit}>
              Donate Now!
            </Button>
          </Form>
        )}
      </Formik>
    )
	}
}

export default injectStripe(CustomCheckoutForm);
//export default CustomCheckoutForm;
